.modalroot{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
    background-color: rgba(0,0,0,0.5);
}

.modalDialog {
  position: relative;
   
    width: auto;
    top: 15%;
    
  }



.warningBox{

    
  
  width: 20vw;
  height: 100%;
  background: #f2f2f2;
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  border:none;
  box-shadow: 0px 5px 10px rgba(0,0,0,.2);
 

  position: relative;
  left: 20%;
    
}
 .exclamation{
     width: 2.5vw;
     height: 5vh;
  font-size: 30px;
  color: #eb9447;
  border: 5px solid #eb9447;
  border-radius: 50%;
  display: flex;
  justify-content: center;
}
.exclamationIcon{
    width: 2vw;
}
 h1{
  font-size: 2vw;
  color: #1b2631;
  margin-bottom: 5px;
}

.btn{
    height: 0px;
    position: absolute;
    top: 0.7vh;
    right: 0.4vw;
}

.btnClose{
    border-radius: 50%;
    border: none;
    height: 2rem;
    background-color: rgba(148, 142, 142, 0.767);
    color: white;
  

}

.btnClose:focus{
    outline: none;
}

.btnClose:hover{
    cursor: pointer;
    background-color:  rgb(158, 150, 150)
}


.modalTransition-appear {
    transition: transform 300ms;
    transform: translateY(-100%);
  }
  
  .modalTransition-appear-active {
    transform: translateY(0);
  }


  @media (min-width: 0px){
    .modalDialog{
      position: relative;
   
    width: auto;
    left: 10%;
